//-------------------------------------------------------------
//Main editor app class, contains most info in state
//-------------------------------------------------------------
class Editor extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      //CURRENTLY NOT USED: values for pixelization and posterize alteration
      pixelValue: 0,
      thresholdValue: 0,
      //value that holds the path to default image, then the base64 data of render
      //base64 used by Jimp, since it is a web-based app. Local files caused issues.
      picture: src,
      bmpPicture: myBMP,
      //preset for bitmap generation
      preset: 0,
      //Locations for crop rectangle
      //TODO: Change names to something less global
      width: 100,
      height: 100,
      top: 20,
      left: 20,
      rotateAngle: 0,
      //Scale values for scale operation
      scaleX: 0,
      scaleY: 0
    };
    this.myRef = React.createRef();
    //TODO: Bind these functions in their declaration, similar to handleX functions
    this.onDrop = this.onDrop.bind(this);
    this.onLinkChange = this.onLinkChange.bind(this);
    this.onScaleFormChangeX = this.onScaleFormChangeX.bind(this);
    this.onScaleFormChangeY = this.onScaleFormChangeY.bind(this);
    this.jimpCrop = this.jimpCrop.bind(this);
    this.jimpScale = this.jimpScale.bind(this);
    this.jimpPixelate = this.jimpPixelate.bind(this);
  }

  //-------------------------------------------------------------
  //Functions to handle events when different buttons are clicked
  //or values are changed
  //-------------------------------------------------------------
  handleDebugButtonClick = (i) => {
    //this.jimpPixelate(this.state.picture);
  }

  handleCropButtonClick = (i) => {
    this.jimpCrop(this.state.picture);
  }

  handleScaleButtonClick = (i) => {
    this.jimpScale(this.state.picture, this.state.scaleX, this.state.scaleY);
  }

  handleGenerateButtonClick = (i) => {
    //TODO:   -change bmpPicture to base64 in another functon,
    //        order seems fucked when doing it in here, most likely issues
    //        with async/sync functions
    //        -Figure out how to reverse adding logo, I.E. revert back to blank template
    //        Maybe save default and revert before adding?

    var preset = this.state.preset;
    console.log(preset);
    console.log("sdfsd");

    jimp.read(this.state.bmpPicture, function(err, bmpImage) {
      jimp.read(this.state.picture, function(err, logo) {
        bmpImage.composite(logo, 20, 20);
        bmpImage.getBase64(Jimp.AUTO, function(err, data){
          this.setState({
            bmpPicture: data,
          });
        }.bind(this));
      }.bind(this));
    }.bind(this));
  }

  handlePresetSelect = (i) => {
    console.log(parseInt(i.target.value, 10));
    this.setState({preset: parseInt(i.target.value, 10)})
  }

  //CURRENTLY NOT USED
  handlePixelChange = (i) => {
    //this.setState({pixelValue: i.target.value});
    this.jimpPixelate(this.state.picture, parseInt(i.target.value, 10));
  }

  //CURRENTLY NOT USED
  handleThresholdChange = (i) => {
    this.setState({thresholdValue: i.target.value});
  }

  handleSubmitButtonClick = (i) => {
    jimp.read(this.state.picture, function(err, image) {
      image.getBase64(Jimp.AUTO, function(err, data){
        this.setState({
          picture: data,
        });
      }.bind(this));
    }.bind(this));
  }

  //These three handlers are for the crop selection rectange
  handleResize = (style, isShiftKey, type) => {
    // type is a string and it shows which resize-handler you clicked
    // e.g. if you clicked top-right handler, then type is 'tr'
    let { top, left, width, height } = style
    top = Math.round(top)
    left = Math.round(left)
    width = Math.round(width)
    height = Math.round(height)
    this.setState({
      top,
      left,
      width,
      height
    })
  }

  handleRotate = (rotateAngle) => {
    this.setState({
      rotateAngle
    })
  }

  handleDrag = (deltaX, deltaY) => {
    this.setState({
      left: this.state.left + deltaX,
      top: this.state.top + deltaY
    })
  }

  //picture is an array passed from image uploader
  //pull most recently uploaded picture from current session
  //TODO: reformat these functions into handleDrop and handleLinkChange
  onDrop(picture){
    this.setState({
      picture: URL.createObjectURL(picture[picture.length - 1]),
    });
  }

  onLinkChange(event){
    this.setState({
      picture: "https://cors-anywhere.herokuapp.com/" + event.target.value.slice(0, -1) + "1"
    });
  }

  onScaleFormChangeX(event){
    //form takes in a string, need to parse into an Int for use in Jimp
    this.setState({
      scaleX: parseInt(event.target.value, 10)
    });
  }

  onScaleFormChangeY(event){
    //form takes in a string, need to parse into an Int for use in Jimp
    this.setState({
      scaleY: parseInt(event.target.value, 10)
    });
  }

  //-------------------------------------------------------------
  //render functions for each visible element
  //TODO: combine everything in the same div into one render function
  //like buttons for control panel and such
  //-------------------------------------------------------------

  //CURRENTLY NOT USED
  renderPixelateSlider(){
    return (
      <PixelateSlider
        value={this.state.pixelValue}
        onChange={this.handlePixelChange}
      />
    );
  }

  //CURRENTLY NOT USED
  renderThresholdSlider(){
    return (
      <ThresholdSlider
        value={this.state.thresholdValue}
        onChange={this.handleThresholdChange}
      />
    );
  }

  renderFinalImage(){
    return (
      <div>
        <DisplayImg
          picture={this.state.picture}
        />
      </div>
    )
  }

  renderBMP(){
    return(
      <DisplayBmpImg
        bmpPicture={this.state.bmpPicture}
      />
    )
  }

  renderDebugButton(i){
    return(
      <DebugButton
        onClick={this.handleDebugButtonClick}
      />
    )
  }

  renderSubmitButton(i){
    return(
      <SubmitButton
        onClick={this.handleSubmitButtonClick}
      />
    )
  }

  renderCropButton(i){
    return(
      <CropButton
        onClick={this.handleCropButtonClick}
      />
    )
  }


  renderScaleButton(i){
    return(
      <ScaleButton
        onClick={this.handleScaleButtonClick}
      />
    )
  }

  renderGenerateButton(i){
    return(
      <GenerateButton
        onClick={this.handleGenerateButtonClick}
      />
    )
  }

  renderScaleForm(i){
    return(
      <ScaleForm
        onChangeX={this.onScaleFormChangeX}
        onChangeY={this.onScaleFormChangeY}
      />
    )
  }

  renderPresetSelector(i){
    return(
      <PresetSelector
        onChange={this.handlePresetSelect}
      />
    )
  }

  renderCropSelector(){
    const {width, top, left, height, rotateAngle} = this.state
    return (
      <div className="App">
        <ResizableRect
          left={left}
          top={top}
          width={width}
          height={height}
          rotateAngle={rotateAngle}
          // aspectRatio={false}
          minWidth={20}
          minHeight={20}
          zoomable='n, w, s, e, nw, ne, se, sw'
          // rotatable={true}
          // onRotateStart={this.handleRotateStart}
          onRotate={this.handleRotate}
          // onRotateEnd={this.handleRotateEnd}
          // onResizeStart={this.handleResizeStart}
          onResize={this.handleResize}
          // onResizeEnd={this.handleUp}
          // onDragStart={this.handleDragStart}
          onDrag={this.handleDrag}
          // onDragEnd={this.handleDragEnd}
        />
      </div>
    )
  }

  renderImageUploader(){
    return(
      <ImageUploader
        withIcon={true}
        buttonText='Choose image'
        label="Max file size: 5mb, Filetypes accepted: .bmp | .jpg | .png"
        fileContainerStyle={{backgroundColor: "grey"}}
        singleImage={true}
        onChange={this.onDrop}
        imgExtension={['.jpg', '.gif', '.png', '.bmp']}
        maxFileSize={5242880}
      />
    )
  }

  renderLinkForm(){
    return(
      <Linkform onChange={this.onLinkChange} onSubmit={this.onLinkSubmit}/>
    )
  }


  //-------------------------------------------------------------
  //Functions to do the actual editing on the image
  //-------------------------------------------------------------
  jimpScale(path, x, y){
    jimp.read(path, function(err, image) {
      image.scaleToFit(x,y);
      image.getBase64(Jimp.AUTO, function(err, data){
        this.setState({
          picture: data,
        });
      }.bind(this));
    }.bind(this));
  }

  jimpResize(path){
    jimp.read(path, function(err, image) {
      image.resize(800,10);
      image.getBase64(Jimp.AUTO, function(err, data){
        this.setState({
          picture: data,
        });
      }.bind(this));
    }.bind(this));
  }

  jimpPixelate(path, x){
    jimp.read(path, function(err, image) {
      image.pixelate(x);
      image.getBase64(Jimp.AUTO, function(err, data){
        this.setState({
          picture: data,
        });
      }.bind(this));
    }.bind(this));
  }

  //TODO: Fix hardcoded margins to automatic,
  //will be a problem when changing app appearance
  jimpCrop(path){
    var localLeft = this.state.left;
    var localTop = this.state.top;
    localLeft = localLeft - 20;
    localTop = localTop - 20;
    jimp.read(path, function(err, image) {
      image.crop(localLeft, localTop, this.state.width, this.state.height);
      image.getBase64(Jimp.AUTO, function(err, data){
        this.setState({
          picture: data,
        });
      }.bind(this));
    }.bind(this));
  }


  //-------------------------------------------------------------
  //Final render of the application
  //-------------------------------------------------------------
  render() {
    return (
      <div>
        <div>
          {this.renderFinalImage()}
          {this.renderBMP()}
          {this.renderCropSelector()}
        </div>
        <div className="panelDisplay">
          {this.renderLinkForm()}
          {this.renderSubmitButton()}
          {this.renderImageUploader()}
          {this.renderDebugButton()}
          {this.renderCropButton()}
          {this.renderScaleForm()}
          {this.renderScaleButton()}
          {this.renderPresetSelector()}
          {this.renderGenerateButton()}
        </div>
      </div>
    );
  }
}


